<template>
  <div class="main_container">

    <div >
      <form @submit.prevent="addTodo">
        <vs-input label-placeholder="Add Todo" v-model="newTodo" type="text" id="newTodo"/>
        <vs-button  type="submit" name="button"> Add </vs-button>
        <vs-button success flat @click="allDone" type="reset" > All done! </vs-button>
      </form>
      
      
    </div>

    <div class="sub_container">
      <div class="list_item">
        <ul>
        <li v-for="todo in todos" v-bind:key="todo.title">
          <input type="checkbox" v-model="todo.done">
          <span :class="{done:todo.done}" > {{ todo.title }}</span>
          <vs-button danger border name="button"  @click="removeTodo(todo)" > Delete </vs-button>
        </li>
      </ul>
      </div>
      
    </div>
  </div>
</template>

<script>
export default {
  name: "Todo",
  data() {
    return {
      newTodo: "",
      todos: [],
    };
  },
  methods: {
    addTodo() {
      this.todos.push({
        title: this.newTodo,
        done: false,
      });
      this.newTodo = "";
    },
    removeTodo(todo){
        const todoIndex = this.todos.indexOf(todo);
        this.todos.splice(todoIndex,1);
    },
    allDone(){
      this.todos.forEach(todo =>{
        todo.done = true;
      });
    }
  },
};
</script>

<style>
.main_container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  
}

form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.sub_container li{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.sub_container span{
  margin-left: 1.25rem;
}

.sub_container ul{
   list-style-type: none;
}

.done{
  text-decoration: line-through;
}

.list_item li{
  background-color: #F4F4F5;
  border-radius: 1rem;
  padding: 0.2rem 2.5rem;
  margin: 1.25rem;
}

</style>
